<template>
  <div class="christmas-activity">
    <div class="draw-btn" v-if="showDraw" @click="drawClick"></div>
    <div class="sign-btn" v-if="!showDraw" @click="signClick"></div>
    <h4 class="title"><span>活动说明</span></h4>
    <ol class="rules">
      <li data-i="1">活动时间： <br>12月23日00：00-12月27日24：00；</li>
      <li data-i="2">参与用户： <br>2019年12月23日前（含23日）认证的投资人；</li>
      <li data-i="3">抽奖条件： <br> 连续签到3天且每天登录【项目工场APP】点评1个项目，即可获得1次抽奖机会；<br> 连续签到5天且每天登录【项目工场APP】点评1个项目，可额外再获得1次抽奖机会；</li>
      <li data-i="4">限时抽奖： <br> 12月25日13：00--14：00； <br> 12月27日13：00--14：00；</li>
      <li data-i="5">奖品福利： <br> 小米手机、沙丘大学—投资学院会员、高定版无线充电器、项目工场虚拟资产—钻石等</li>
      <li data-i="6">奖品兑换： <br> 请联系投资人小助手微信（15911030548）兑换； <br> 截止时间为12月31日24点</li>
      <li data-i="7">活动最终解释权，归清科项目工场所有；</li>
    </ol>
    <!-- login-dialog -->
    <pem-dia-log type="modal" ref="loginDialog" :showCloseBtn="false">
      <div class="login-wrapper">
        <div class="login-inner">
          <div class="close-btn" @click="closeLogin"></div>
          <div class="title">亲，请先输入手机号~</div>
          <label class="input-item">
            <div class="input-title">手机号</div>
            <input class="phone-input" v-model="phone" placeholder="请输入手机号" type="tel" maxlength="11" @input="telInput" >
          </label>
          <div class="input-item">
            <label>
              <div class="input-title">验证码</div>
              <input class="code-input" v-model="code" placeholder="请输入验证码" type="tel" maxlength="6" @input="telInput">
            </label>
            <div class="code-btn" :class="{'enabled': codeEnabled}" @click="getCode">{{ codeText }}</div>
          </div>
          <div class="login-btn" :class="{'enabled': submitEnabled }" @click="goLogin">提交</div>
        </div>
      </div>
    </pem-dia-log>
    <!-- note-dialog -->
    <pem-dia-log type="modal" ref="noteDialog" :showCloseBtn="false">
      <div class="note-wrapper">
        <div class="close-btn" @click="noteDialogHide"></div>
        <h4 class="note-title">温馨提示</h4>
        <p class="note-text" v-html="noteText"></p>
        <p class="note-btn" @click="noteDialogSure">{{ '好的' }}</p>
      </div>
    </pem-dia-log>
    <!-- toast -->
    <pem-toast ref="toastEle" :text="toastText"></pem-toast>
  </div>
</template>

<script type="text/ecmascript-6">
import PemScrollView from 'components/scroll-view/scroll-view'
import PemToast from 'components/pem-toast/pem-toast'
import PemDiaLog from 'components/dialog/dialog'
import { MiniSiteApi } from 'api'
import { setTitle, validatePhone } from 'common/js/util'
import storage from 'common/js/storage.js'

export default {
  data () {
    return {
      sdphone: storage.get('sdphone') || '',
      showDraw: false,
      phone: null,
      code: null,
      codeText: '获取验证码',
      codeEnabled: false,
      submitEnabled: false,
      time: 60,
      timer: null,
      toastText: '',
      noteType: 0,
      noteText: ''
    }
  },
  components: {
    PemScrollView,
    PemToast,
    PemDiaLog
  },
  created () {
    this.initPage()
  },
  methods: {
    initPage () {
      setTitle('圣诞嘉年华')
      this.wxShare({
        title: '项目工场【投资人圣诞嘉年华】🎉🎉🎉',
        content: '圣诞大趴 ，壕礼任性领！！！',
        imgUrl: 'http://cdn.pemarket.com.cn/operation/christmas-share-icon.png',
        linkUrl: `${window.location.origin}/christmas-activity`
      })
      if (this.sdphone) {
        if (this.sdphone === '15201177607') {
          this.sdphone = '15810857502'
          storage.set('sdphone', '15810857502')
        }
        this.getButtonStatus()
      }
    },
    drawClick () {
      window.location.href = `${window.location.origin}/christmas-draw`
    },
    signClick () {
      if (this.sdphone) {
        this.gosign()
      } else {
        this.showLogin()
      }
    },
    showLogin () {
      this.$refs.loginDialog.show()
    },
    closeLogin () {
      this.$refs.loginDialog.hide()
    },
    telInput () {
      this.phone = (this.phone || '').trim()
      this.code = (this.code || '').trim()
      this.codeEnabled = !!this.phone
      this.submitEnabled = !!(this.phone && this.code)
    },
    getButtonStatus () {
      if (!this.sdphone) return
      this._getButtonStatus({'mobile': this.sdphone}, (data) => {
        if (!data) return
        if (+data.resultcode === -12) {
          this.showDraw = true
        }
      })
    },
    gosign () {
      if (!this.sdphone) return
      this._gosign({'mobile': this.sdphone}, (data) => {
        this.noteType = +(data && data.resultcode) || 0
        if ((+data.resultcode === -16 || +data.resultcode === -18) && +data.candraw === 1) {
          this.showDraw = true
        }
        this.noteDialogShow()
      })
    },
    getCode () {
      if (!this.codeEnabled) return
      if (!validatePhone(this.phone)) {
        this.showToast('请输入正确的手机号')
        return
      }
      this.codeEnabled = false
      this.codeText = `${this.time}s后重试`
      this.timer = setInterval(() => {
        this.codeText = `${this.time}s后重试`
        if ((this.time--) <= 0) {
          this.time = 60
          this.codeText = '获取验证码'
          this.codeEnabled = true
          clearInterval(this.timer)
        }
      }, 1000)
      this._checkMobile(this.phone, (data) => {
        if (data.verifyCode) {
          this.code = data.verifyCode.replace(',repeat', '')
          this.telInput()
        }
        if (+data.resultcode === 1) {
          this.codeEnabled = false
        } else {
          let notemsg = {
            '-11': '手机号为空',
            '-12': '手机号输入不正确',
            '-13': '获取验证码次数太多',
            '-14': '获取验证码间隔太短'
          }[data.resultcode] || '未知错误，请联系管理员'
          this.showToast(notemsg)
          this.codeEnabled = true
        }
      })
    },
    goLogin () {
      if (!this.submitEnabled) {
        return
      }
      let regCode = /^\d{6}$/
      if (!validatePhone(this.phone)) {
        this.showToast('请输入正确的手机号')
        return
      }
      if (!regCode.test(this.code)) {
        this.showToast('请输入正确的验证码')
        return
      }
      let options = {
        mobile: this.phone,
        code: this.code
      }
      this._login_lx(options, (data) => {
        if (+data.resultcode === 1) {
          this.sdphone = this.phone
          storage.set('sdphone', this.phone)
          this.closeLogin()
        } else {
          let notemsg = {
            '-11': '手机号为空',
            '-12': '验证码为空',
            '-13': '验证码失效',
            '-14': '验证码错误'
          }[data.resultcode] || '未知错误，请联系管理员'
          this.showToast(notemsg)
        }
      })
    },
    showToast (txt) {
      if (!txt) return
      this.toastText = txt
      this.$refs.toastEle.show()
    },
    hideToast () {
      this.$refs.toastEle.hide()
    },
    noteDialogShow () {
      this.noteText = {
        '0': '签到失败,请重试!',
        '-11': '亲，不在活动期间哦~',
        '-12': '亲，本次活动您暂不符合参与条件，欢迎您参加下次活动~',
        '-13': '恭喜您签到成功，您今天还需点评1个项目哟~',
        '-14': '恭喜，签到成功，请明天继续哟，坚持就是胜利~',
        '-15': '亲，您今天已经签到了喽~',
        '-16': '恭喜！您已获得抽奖资格，抽奖时间：<br>12月25日中午13：00--14：00期间<br>请准时过来抽哟~',
        '-17': '亲，恭喜签到成功！您已错过今天的抽奖，不过再坚持签到2天，12月27日13:00--14:00还有1次抽奖机会，请准时过来哟~',
        '-18': '恭喜！您已获得抽奖资格，抽奖时间：<br>12月27日中午13：00--14：00期间<br>请准时过来抽奖哟~',
        '-19': '亲，恭喜签到成功！您已错过今天的抽奖，欢迎您继续参加下次活动~'
      }[this.noteType]
      if (!this.noteText) return
      this.$refs.noteDialog.show()
    },
    noteDialogHide () {
      this.$refs.noteDialog.hide()
    },
    noteDialogSure () {
      if (this.showDraw) {
        window.location.href = `${window.location.origin}/christmas-draw`
      } else {
        this.noteDialogHide()
      }
    },
    _getButtonStatus (options, callback) {
      MiniSiteApi.getButtonStatus(options).then(data => {
        callback(data)
      })
    },
    _gosign (options, callback) {
      MiniSiteApi.gosign(options).then(data => {
        callback(data)
      })
    },
    _checkMobile (mobile, callback) {
      MiniSiteApi.checkMobel(mobile).then(data => {
        callback(data)
      })
    },
    _login_lx (options, callback) {
      MiniSiteApi.login_lx(options).then(data => {
        callback(data)
      })
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"
  @keyframes btnAnimate
    0%
      -webkit-transform:scale(1);
      transform:scale(1)
    to
      -webkit-transform:scale(1.1);
      transform:scale(1.1)
  ::-webkit-input-placeholder
    color: #A7A7A7;
  ::-moz-placeholder
    color: #A7A7A7;
  ::-webkit-input-placeholder
    color: #A7A7A7;
  ::-ms-input-placeholder
    color: #A7A7A7;
  .christmas-activity
    position: relative
    font-size:0;
    padding: 10.46rem 0 0.80rem
    bg-image('./images/bt')
    background-size: 100%;
    background-repeat: no-repeat
    background-color: #9B0507;
    max-width: 750px;
    margin: 0 auto;
    .sign-btn, .draw-btn
      position: absolute
      top: 8.86rem
      left: 2.06rem
      width: 3.40rem
      height: 1.28rem
      background-size: 100% 100%
      background-repeat: no-repeat
      -webkit-animation: btnAnimate .4s infinite alternate forwards;
      animation: btnAnimate .4s infinite alternate forwards;
    .sign-btn
      bg-image('./images/btn-sign')
    .draw-btn
      bg-image('./images/btn-draw')
    .title
      padding-bottom: 0.24rem
      text-align: center
      span
        position: relative
        display: inline-block
        font-family: PingFangSC-Medium;
        font-size: 0.32rem
        color: #FFFFFF;
        line-height: 0.44rem 
        padding: 0 0.28rem
        &::before, &::after
          content: ''
          position: absolute
          top: 50%
          margin-top: -0.04rem
          background: #FFFFFF;
          width: 0.08rem
          height: 0.08rem
          border-radius: 100%
        &::before
          left: 0
        &::after
          right: 0
    .rules
      font-family: PingFangSC-Regular;
      font-size: 0.24rem;
      color: #FFFFFF;
      line-height: 0.38rem
      li
        position: relative
        padding: 0 0.34rem 0.20rem 0.74rem
        &.no-order
          &::before
           display: none
        &::before
          content: "" attr(data-i) "、"
          position: absolute
          top: 0
          left: 0.34rem
    .login-wrapper
      .login-inner
        width: 295px
        height: 215px
        background: #ffffff
        border-radius: 8px
        .close-btn
          position: absolute
          top: 0
          right: 0
          width: 42px
          height: 42px
          bg-image('./images/close')
          background-size: 12px 12px
          background-repeat: no-repeat
          background-position: center
        .title
          font-family: PingFangSC-Regular;
          font-size: 15px;
          color: #202020;
          text-align: center
          padding: 32px 0 25px
        .input-item
          position: relative
          display: block
          min-height: 26px 
          padding-bottom: 20px
          .input-title
            position absolute
            left: 24px
            top: 0
            font-size: 13px;
            color: #333333;
            line-height: 26px
            padding-right: 8px
            bg-image('./images/input-require')
            background-size: 7px 9px
            background-repeat: no-repeat
            background-position: right center
          input
            box-sizing: border-box
            position absolute
            left: 80px
            background: #F5F5F5;
            border-radius: 2px;
            width: 191px;
            height: 26px;
            line-height: 26px;
            font-size: 11px;
            padding: 5px 8px
            &.code-input
              width: 115px
          .code-btn
            position absolute
            right: 24px
            top: 0
            width: 70px
            height: 25px
            line-height: 25px
            text-align: center
            border: 0.5px solid #A7A7A7;
            border-radius: 2px;
            color: #A7A7A7
            font-size: 12px
            &.enabled
              color: #FFC131
              border-color: #FFC131
        .login-btn
          position: absolute
          bottom : 0
          left: 0
          width: 100%
          height: 45px
          line-height: 45px
          border-top: 1px solid #DBDBDB
          font-family: PingFangSC-Regular;
          font-size: 15px;
          color: #A7A7A7;
          letter-spacing: -0.05px;
          text-align: center
          &.enabled
            color: #FFD100;
    .note-wrapper
      box-sizing: border-box
      width: 295px
      background-color: #ffffff
      padding: 32px 35px 46px
      text-align: center
      border-radius: 8px;
      .note-title
        font-size: 15px
        font-weight: bold
        color: #202020;
      .note-text
        font-size: 13px
        color: #333333
        line-height: 20px 
        padding: 25px 0
      .note-btn
        position: absolute
        left: 0
        bottom: 0
        width: 100%
        height: 45px
        line-height: 45px
        font-size: 15px;
        color: #FFC131;
        border-top: 0.5px solid #DBDBDB;
      .close-btn
        position: absolute
        top: 0
        right: 0
        width: 42px
        height: 42px
        bg-image('./images/close')
        background-size: 12px 12px
        background-repeat: no-repeat
        background-position: center
</style>



